.brk-btn {
  font-size: 20px;
  font-weight: 400;
  position: relative;
  background: none;
  color: red;
  text-transform: uppercase;
  text-decoration: none;
  border: 0.2em solid red;
  padding: 0.5em 1em;
  margin-left: 1em;
  margin-right: 1em;
  position: relative;
  z-index: 4;
}
.brk-btn::before {
  content: "";
  display: block;
  position: absolute;
  width: 10%;
  background: black;
  height: 0.3em;
  right: 20%;
  top: -0.21em;
  -webkit-transform: skewX(-45deg);
          transform: skewX(-45deg);
  -webkit-transition: all 0.45s cubic-bezier(0.86, 0, 0.07, 1);
  -webkit-transition: all 0.2s cubic-bezier(0.86, 0, 0.07, 1);
  transition: all 0.2s cubic-bezier(0.86, 0, 0.07, 1);
}
.brk-btn::after {
  content: "";
  display: block;
  position: absolute;
  width: 10%;
  background: black;
  height: 0.3em;
  left: 20%;
  bottom: -0.25em;
  -webkit-transform: skewX(45deg);
          transform: skewX(45deg);
  -webkit-transition: all 0.45 cubic-bezier(0.86, 0, 0.07, 1);
  -webkit-transition: all 0.2s cubic-bezier(0.86, 0, 0.07, 1);
  transition: all 0.2s cubic-bezier(0.86, 0, 0.07, 1);
}
.brk-btn:hover::before {
  right: 80%;
}
.brk-btn:hover::after {
  left: 80%;
}

