/* Stars ------------------------------------- */

#star_field,
.stars {
  overflow: hidden;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.stars {
  background-image: -webkit-radial-gradient(
      20px 30px,
      2px 2px,
      #eee,
      rgba(0, 0, 0, 0)
    ),
    -webkit-radial-gradient(40px 70px, 2px 2px, #fff, rgba(0, 0, 0, 0)),
    -webkit-radial-gradient(50px 160px, 2px 2px, #ddd, rgba(0, 0, 0, 0)),
    -webkit-radial-gradient(90px 40px, 2px 2px, #fff, rgba(0, 0, 0, 0)),
    -webkit-radial-gradient(130px 80px, 2px 2px, #fff, rgba(0, 0, 0, 0)),
    -webkit-radial-gradient(160px 120px, 2px 2px, #ddd, rgba(0, 0, 0, 0));
  background-image: radial-gradient(
      2px 2px at 20px 30px,
      #eee,
      rgba(0, 0, 0, 0)
    ),
    radial-gradient(2px 2px at 40px 70px, #fff, rgba(0, 0, 0, 0)),
    radial-gradient(2px 2px at 50px 160px, #ddd, rgba(0, 0, 0, 0)),
    radial-gradient(2px 2px at 90px 40px, #fff, rgba(0, 0, 0, 0)),
    radial-gradient(2px 2px at 130px 80px, #fff, rgba(0, 0, 0, 0)),
    radial-gradient(2px 2px at 160px 120px, #ddd, rgba(0, 0, 0, 0));
  background-repeat: repeat;
  background-size: 200px 200px;
  -webkit-animation: zoom 30s infinite;
          animation: zoom 30s infinite;
  opacity: 0;
  z-index: 1;
}

.stars:nth-child(1) {
  background-position: 50% 50%;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
.stars:nth-child(2) {
  background-position: 20% 60%;
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
}
.stars:nth-child(3) {
  background-position: -20% -30%;
  -webkit-animation-delay: 12s;
          animation-delay: 12s;
}
.stars:nth-child(4) {
  background-position: 40% -80%;
  -webkit-animation-delay: 18s;
          animation-delay: 18s;
}
.stars:nth-child(5) {
  background-position: -20% 30%;
  -webkit-animation-delay: 24s;
          animation-delay: 24s;
}

@-webkit-keyframes zoom {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  85% {
    opacity: 1;
    -webkit-transform: scale(2.8);
            transform: scale(2.8);
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
  }
  100% {
    opacity: 0;
    -webkit-transform: scale(3.5);
            transform: scale(3.5);
  }
}

@keyframes zoom {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  85% {
    opacity: 1;
    -webkit-transform: scale(2.8);
            transform: scale(2.8);
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
  }
  100% {
    opacity: 0;
    -webkit-transform: scale(3.5);
            transform: scale(3.5);
  }
}

